.container {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.header {
 width: 100%;
 height: 10vh;
 display: flex;
 justify-content: center;
 align-items: center;
 font-size: 3vh;
 background-color: #353535FF;
 color: white;
}

.menu {
	width: 40%;
	height: 5vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: #515151FF;
	color: white;
}

.menu > a {
	color: white;
	text-decoration: none;
}

.body {
	height: 75vh;
	width: 90%;
	overflow-y: auto;
}