.container {
	height:100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.header {
	width: 100%;
	background: black;
	color: white;
	text-align: center;
}

.menu {
	width: 100%;
	display: flex;
	justify-content: center;
}

.list {
	width: 50%;
	display: flex;
	justify-content: space-between;
}

.listitem {
	display: inline;
}

.body {
	height: 80vh;
	width: 95%;
	overflow-y: auto;
}